import * as React from 'react';
import {observer} from 'mobx-react';
import {nav, Page, Context, Form, Schema, UiSchema, UiTextItem} from 'tonva';
import {store} from '../store';

@observer
export default class NewFellowPage extends React.Component<{isOwner:boolean, isAdmin:boolean}> {
    //private form: TonvaForm;
    private schema: Schema = [
        {name:'user', type:'string', required:true},
        {name:'submit', type:'submit'}
    ]
    private uiSchema: UiSchema = {
        items: {
            user: {widget:'text', maxLength: 100, placeholder: '', label: ''} as UiTextItem,
            submit: {widget:'button', label: '', className: 'btn btn-primary'}
        }
    }
    /*
    private formRows:FormRow[] = [
        {
            label: '',
            field: {
                type: 'string',
                name: 'user',
                required: true,
                maxLength: 100,
            },
            face: {
                type: 'string',
                placeholder: '',
            }
        },
    ];
    private schema:FormSchema = new FormSchema({
        fields: [
            {
                type: 'string',
                name: 'user',
                placeholder: '',
                label: '',
                rules: ['required', 'maxlength:100']
            },
        ],
        onSumit: this.onSendInvitation,
        submitText: ''
    });
    */
    private onSendInvitation = async (name:string, context:Context): Promise<string> => {
        let {isOwner, isAdmin} = this.props;
        let user = context.form.data['user'];
        let ret = await store.admins.addNew(user, isOwner?1:0, isAdmin?1:0);
        if (ret !== undefined) {
            nav.pop();
            return;
        }
        return user + '';
        /*
        let msg = {a: 1, b: 'ddd'};
        let toName = values['user'];
        let userId = await mainApi.userId(toName);
        if (userId === 0) {
            this.form.formView.setError('user', '\'' + toName +  '\' ');
            return;
        }
        await mainApi.sendMessage(userId, 'unit-follow-invite', msg);
        nav.replace(<Page header='' back="close">
            <Card>
                <CardBody>
                    <CardTitle></CardTitle>
                    <CardText>{toName}</CardText>
                    <Button color='primary' onClick={()=>nav.back()}></Button>
                </CardBody>
            </Card>
        </Page>);
        */
    }
    render() {
        let {isOwner, isAdmin} = this.props;
        let caption = isOwner? "" : "";
        return <Page header={'' + caption}>
            <div className="container">
                <div className='my-4 text-info'>
                    {caption}
                </div>
                <Form schema={this.schema} uiSchema={this.uiSchema} onButtonClick={this.onSendInvitation}/>
            </div>
        </Page>;
    }
}
